@charset "UTF-8";
body {
  margin: 0;
}

@media (min-width: 320px) {
  .page-body {
    font-family: "Ubuntu", sans-serif;
    font-weight: 300;
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 320px;
    margin: 0 auto;
    position: relative;
  }
  .page-body::after {
    content: "";
    position: absolute;
    background-image: url(../../img/mobile-intro-bg.jpg);
    background-repeat: no-repeat;
    width: 320px;
    height: 568px;
    z-index: -1;
  }
}
@media (min-width: 320px) {
  .page-header {
    display: flex;
    width: 280px;
    justify-content: space-between;
    margin-top: 20px;
  }

  .page-header__logo {
    width: 28px;
  }

  .page-header__button {
    width: 108px;
    height: 34px;
  }

  .page-header__nav-toggle {
    font-family: "Ubuntu", sans-serif;
    font-weight: 600;
    color: #FFFFFF;
    cursor: pointer;
    border: none;
    background-color: transparent;
    text-transform: uppercase;
    position: relative;
    width: 24px;
    background-color: #FFFFFF;
    height: 2px;
    box-shadow: 0 8px 0 #FFFFFF;
  }
  .page-header__nav-toggle::after {
    content: "";
    position: absolute;
    width: 16px;
    height: 2px;
    background-color: #FFFFFF;
    top: 16px;
    left: 8px;
  }
}
@media (min-width: 320px) {
  .logo {
    background-image: url("../../img/digpl-logo.svg");
    background-repeat: no-repeat;
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@media (min-width: 320px) {
  .order-button {
    font-family: "Ubuntu", sans-serif;
    font-weight: 600;
    color: #FFFFFF;
    cursor: pointer;
    border: none;
    background-color: transparent;
    text-transform: uppercase;
    outline: 2px solid #0DD1A2;
    border-radius: 2px;
    font-size: 14px;
  }
}
@media (min-width: 320px) {
  .nav--closed {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
  }
}
@media (min-width: 320px) {
  .nav-list {
    list-style-type: none;
  }
}
@media (min-width: 320px) {
  .page-main {
    width: 288px;
    margin: 0 auto;
  }
}
@media (min-width: 320px) {
  .intro {
    margin-top: 90px;
  }

  .intro__title {
    margin: 0;
    font-size: 36px;
    font-weight: 400;
    color: #FFFFFF;
    text-align: center;
  }

  .intro__description {
    margin: 20px 0 0 0;
    font-size: 15px;
    font-weight: 300;
    line-height: 21px;
    color: #FFFFFF;
    text-align: center;
  }

  .intro__buttons {
    margin: 50px auto 0 auto;
  }

  .intro__scroll-button {
    font-family: "Ubuntu", sans-serif;
    font-weight: 600;
    color: #FFFFFF;
    cursor: pointer;
    border: none;
    background-color: transparent;
    text-transform: uppercase;
    width: 288px;
    margin-top: 57px;
    font-size: 11px;
    font-weight: 400;
    letter-spacing: 2px;
    opacity: 0.5;
    position: relative;
  }
  .intro__scroll-button::before {
    content: "";
    width: 7px;
    height: 1px;
    position: absolute;
    background-color: #FFFFFF;
    opacity: 0.5;
    transform: rotate(45deg);
    top: 25px;
    left: 137px;
  }
  .intro__scroll-button::after {
    content: "";
    width: 7px;
    height: 1px;
    position: absolute;
    background-color: #FFFFFF;
    opacity: 0.5;
    transform: rotate(-45deg);
    top: 25px;
    left: 142px;
  }
}
@media (min-width: 320px) {
  .intro-buttons {
    display: flex;
    flex-direction: column;
    width: 207px;
  }

  .intro-buttons__order {
    font-family: "Ubuntu", sans-serif;
    font-weight: 600;
    color: #FFFFFF;
    cursor: pointer;
    border: none;
    background-color: transparent;
    text-transform: uppercase;
    width: 207px;
    height: 55px;
    background-color: #15B38D;
    position: relative;
    border-radius: 3px;
  }
  .intro-buttons__order::after {
    content: "Заказать";
    position: absolute;
    top: 18px;
    left: 72px;
    font-size: 18px;
    line-height: 21px;
    font-weight: 700;
  }
  .intro-buttons__order::before {
    content: "";
    position: absolute;
    top: 15px;
    left: 35px;
    width: 25px;
    height: 25px;
    z-index: 1;
    background-image: url("../../img/order-btn-icon.svg");
    background-repeat: no-repeat;
  }

  .intro-buttons__video {
    margin-top: 12px;
    font-family: "Ubuntu", sans-serif;
    font-weight: 600;
    color: #FFFFFF;
    cursor: pointer;
    border: none;
    background-color: transparent;
    text-transform: uppercase;
    width: 207px;
    height: 55px;
    border: 3px solid #15B38D;
    border-radius: 3px;
    position: relative;
  }
  .intro-buttons__video::after {
    content: "Видео";
    position: absolute;
    top: 15px;
    left: 88px;
    font-size: 18px;
    line-height: 21px;
    font-weight: 700;
  }
  .intro-buttons__video::before {
    content: "";
    position: absolute;
    top: 13px;
    left: 49px;
    width: 25px;
    height: 25px;
    z-index: 1;
    background-image: url("../../img/video-btn-icon.svg");
    background-repeat: no-repeat;
  }
}
@media (min-width: 320px) {
  .about {
    position: relative;
  }
  .about::before {
    content: "";
    position: absolute;
    background-image: url("../../img/index-bg.jpg");
    background-attachment: fixed;
    background-repeat: no-repeat;
    width: 320px;
    height: 568px;
    z-index: -1;
    top: 32px;
    left: -16px;
  }

  .about__title {
    margin: 0;
  }
}